<template>
  <div class="add-reg-manage">
    <Form
      ref="formValidate"
      :model="formValidate"
      :rules="ruleValidate"
      :label-width="100"
      class="form-content"
    >
      <FormItem label="标题" prop="title">
        <Input v-model="formValidate.title" placeholder="请输入标题"></Input>
      </FormItem>
      <FormItem label="是否公开">
        <i-switch v-model="formValidate.ifShow" @on-change="changeUseSts(formValidate.ifShow)">
          <span slot="open">是</span>
          <span slot="close">否</span>
        </i-switch>
      </FormItem>
      <FormItem label="发布机构" prop="pubOrg">
        <Select v-model="formValidate.pubOrg" placeholder="请输入发布机构">
          <Option value="">未选择</Option>
          <Option v-for="item in pubOrgDatas" :value="item.code" :key="item.code">{{item.name}}</Option>
        </Select>
      </FormItem>
      <FormItem label="发布时间" prop="pubTime">
        <DatePicker
          type="date"
          placeholder="请选择发布时间"
          :value="formValidate.pubTime"
          @on-change="formValidate.pubTime=$event"
        ></DatePicker>
      </FormItem>
      <FormItem label="编辑内容" prop="content">
        <Input
          v-model="formValidate.content"
          type="textarea"
          :autosize="{minRows: 2,maxRows: 5}"
          placeholder="请输入编辑内容"
        ></Input>
      </FormItem>
      <FormItem label="附件">
        <showUsualFiles :file-obj="fileObj" :key="fileId" @requestUploadList="receiveUploadList"></showUsualFiles>
      </FormItem>
      <FormItem>
        <div class="button-content mL_0">
          <Button type="primary" @click="handleSubmit('formValidate')" :disabled="buttonDisabled">保存</Button>
          <Button @click="handleCancel">取消</Button>
        </div>
      </FormItem>
    </Form>
  </div>
</template>
<script>
import showUsualFiles from "@/components/commonComp/showUsualFiles";
export default {
  components: {
    showUsualFiles
  },
  props: ["dataObj"],
  data() {
    return {
      buttonDisabled:false,
      fileObj:{},
      uploadList: [],
      fileId:"",
      formValidate: {
        title: "", //标题
        pubOrg: "", //发布机构
        pubTime: "", //发布时间
        ifShow: false, //是否公开
        content: "", //编辑内容
        attachId: "" //文件id
      },
      pubOrgDatas: [],
      index: 1,
      ruleValidate: {
        title: [
          {
            required: true,
            message: "标题不能为空！",
            trigger: "blur"
          },
           { max:125, message: '输入长度过长', trigger: 'blur' },
        ],
        pubOrg: [
          {
            required: true,
            message: "发布机构不能为空！",
            trigger: "change"
          }
        ],
        pubTime: [
          {
            required: true,
            message: "发布时间不能为空！",
            trigger: "change"
          }
        ],
        content: [
          {
            required:false,
            message: '编辑内容不能超过1000字',
            trigger: 'blur',
            max:1000
          },
        ],
      }
    };
  },
  created() {
    this.pubOrgDatas.length > 0 ? "" : this.getDic();
    // 1:新增 2修改
    if (this.dataObj.type == "2") {
      let datas = this.dataObj.data;
      if(datas && datas.id){
        this.getInfInfoRow(datas.id);
      }
    }else{
      this.fileObj = {
        data:[],
        showFlag:true
      }
    } 
  },
  mounted() {},
  methods: {
    receiveUploadList(list) {
      this.uploadList = [];
      list.forEach(item => {
        this.uploadList.push(item.url);
      });
      this.formValidate.attachId = this.uploadList;
      console.log("receiveUploadlist", this.uploadList);
    },
    getInfInfoRow(id) {
      this.axios
      .post("/process/infInfo/getInfInfoRow/"+id, {
        headers: { "Content-Type": "application/json" }
      })
      .then(response => {
        if (response.code == "200") {
          let item = response.data;
          this.formValidate = {
            attachId:item.attachId,
            title: item.title, //标题
            pubOrg: item.pubOrg, //发布机构
            pubTime: item.pubTime, //发布时间
            ifShow: item.ifShow == "true" ? true : false, //是否公开
            content: item.content, //编辑内容
            id:item.id
          };
          this.fileObj = {
            data:item,
            showFlag:true,
          }
          this.fileId = item.busId;
        }
      })
      .catch(error => {});

    },
    /**
     * name:获取下拉字典
     */
    getDic() {
      this.axios
        .get("/system/getDic/PROCESS_PUB_ORG")
        .then(response => {
          if (response.code == "200") {
            this.pubOrgDatas = response.data.PROCESS_PUB_ORG;
          }
        })
        .catch(function(error) {});
    },
    changeUseSts(flg) {
      this.formValidate.ifShow = flg;
    },
    /**
     * name:保存新增信息
     **/
    handleSubmit(name) {
      console.log(this.formValidate);
      this.$refs[name].validate(valid => {
        if (valid) {
          // 新增
          if(this.dataObj.type == "1"){
             this.buttonDisabled = true;
            this.axios
              .post("/process/infInfo/addInfInfo", this.formValidate, {
                headers: { "Content-Type": "application/json" }
              })
              .then(response => {
                if (response.code == "200") {
                   this.buttonDisabled = false;
                  this.$emit("save");
                  this.$Message.success("保存成功！");
                }
              })
              .catch(error => {
                this.buttonDisabled = false;
              });
          }else{
            // 修改
             this.buttonDisabled = true;
            this.axios
              .post("/process/infInfo/updateInfInfo", this.formValidate, {
                headers: { "Content-Type": "application/json" }
              })
              .then(response => {
                if (response.code == "200") {
                  this.buttonDisabled = false;
                  this.$Message.success("修改成功！");
                  this.$emit("save");
                }
              })
              .catch(error => {
                this.buttonDisabled = false;
              });
          }
        }
      });
    },
    handleCancel(name) {
      this.$emit("cancel", false);
    }
  }
};
</script>
<style lang="scss" @scoped>
.add-reg-manage {
  margin: 0 30px;
  label {
    font-size: 14px;
  }
  .form-content {
    .speical-span {
      width: 8.33333333%;
      float: left;
    }
    .ivu-date-picker {
      width: 100%;
    }
    .special-div {
      width: 100px;
      font-size: 14px;
      padding: 10px 12px 10px 0;
      text-align: right;
    }
    .special-form {
      margin: 0 0 20px -20px;
      border: 1px solid #eeeeee;
      padding: 20px 20px 0px 0px;
    }
  }
}
</style>


